import compute from 'css-layout';
export function computeLayout(styleNode) {
  var stack = [styleNode];

  while (stack.length) {
    var node = stack.pop();
    node.children = node.originChildren.filter(function (child) {
      if (child.style.display !== 'none') {
        stack.push(child);
        return true;
      }

      return false;
    });
  }

  compute(styleNode);
}